import React from 'react'

const TechicalSkills = props => (
  <section id="techical-skills" className="main special">
    <header className="major">
      <h2 className=" "> Technical Skills</h2>
    </header>

    <ul className="features">
      <li>
        <div>
          <span className="icon major style3 fa-code"></span>
          <h2>Languages</h2>
          <ul className="list">
            <li>Python</li>
            <li>JavaScript</li>
            <li>C#</li>
            <li>Java</li>
          </ul>
        </div>
      </li>
      <li>
        <div>
          <span className="icon major style1 fa-rocket"></span>
          <h2 className="">Libs and Works</h2>
          <ul className="list">
            <li>React/Redux</li>
            <li>.Net Core</li>
            <li>HTML</li>
            <li>Less/SASS</li>
            <li>CSS</li>
            <li>jQuery</li>
            <li>Gatsby</li>
            <li>GraphQL</li>
            <li>GraphiQL</li>
          </ul>
        </div>
      </li>
      <li>
        <div>
          <span className="icon major style5 fa-wrench"></span>
          <h2>Box of Tools</h2>
          <ul className="list">
            <li>ServiceNow</li>
            <li>Confluence</li>
            <li>Jira</li>
            <li>SharePoint</li>
            <li>Version Control</li>
            <li>Git</li>
            <li>NPM</li>
            <li>Putty/ssh</li>
            <li>Linux/Bash</li>
          </ul>
        </div>
      </li>
    </ul>
  </section>
)

export default TechicalSkills
