import React from 'react'
import Scrollspy from 'react-scrollspy'
import Scroll from './Scroll'
import logo from '../assets/images/website-icon.png'

const Nav = props => (
  <nav id="nav" className={props.sticky ? 'alt' : ''}>
    <section className="left hide-when-small">
      <a href="/" className="small image">
        <img href="/" src={logo} />
      </a>
    </section>
    <Scrollspy
      className="hide-when-small"
      items={['intro', 'first', 'second', 'cta']}
      currentClassName="is-active"
      offset={-300}
    >
      <li>
        <Scroll type="id" element="intro">
          <a href="#">Introduction</a>
        </Scroll>
      </li>
      <li>
        <Scroll type="id" element="first">
          <a href="#">First Section</a>
        </Scroll>
      </li>
      <li>
        <Scroll type="id" element="second">
          <a href="#">Second Section</a>
        </Scroll>
      </li>
      <li>
        <Scroll type="id" element="cta">
          <a href="#">Get Started</a>
        </Scroll>
      </li>
    </Scrollspy>
    <section className="right">
      <ul>
        <li>
          <a className="highlight" href="/Say-Hello">
            Say Hello
          </a>
        </li>
      </ul>
    </section>
  </nav>
)

export default Nav
